import { Github, MessageSquare, Send, Mail } from 'lucide-react';
import Link from 'next/link';
import clsx from 'clsx';

export default function HeroSection() {
    return (
        <div className={clsx(
            "w-full rounded-3xl p-8 md:p-12",
            "bg-[#111111]/90 backdrop-blur-md",
            "border border-neon-purple/50",
            "shadow-[0_0_30px_rgba(217,70,239,0.2)]",
            "flex flex-col md:flex-row items-center gap-8 md:gap-12 text-center md:text-left"
        )}>
            {/* Avatar */}
            <div className="relative group shrink-0">
                <div className="absolute inset-0 bg-neon-green rounded-full blur-2xl opacity-40 group-hover:opacity-60 transition-opacity" />
                <div className="relative w-64 h-64 md:w-80 md:h-80 rounded-full border-4 border-neon-green shadow-[0_0_40px_rgba(0,255,159,0.5)] overflow-hidden">
                    <img
                        src="/avatar_v2.jpg"
                        alt="Profile Avatar"
                        className="w-full h-full object-cover transform group-hover:scale-110 transition-transform duration-500"
                    />
                </div>
            </div>

            {/* Content */}
            <div className="flex-1 flex flex-col items-center md:items-start space-y-4">
                <h1 className="text-3xl md:text-5xl font-bold text-white">
                    <span className="text-neon-green">Welcome to</span> <br className="hidden md:block" />
                    AI Prompt Space
                </h1>

                <div className="flex flex-wrap gap-2 justify-center md:justify-start">
                    {["Cloud Shu's AI Notes", "Product Manager", "Community Creator"].map((tag, i) => (
                        <span key={i} className="px-3 py-1 rounded-full text-xs font-mono bg-white/5 text-gray-400 border border-white/10">
                            {tag}
                        </span>
                    ))}
                </div>

                {/* Action Buttons */}
                <div className="flex gap-4 pt-4">
                    {[
                        { icon: MessageSquare, label: "WeChat", color: "hover:border-green-500 hover:text-green-500" },
                        { icon: Send, label: "Instant", color: "hover:border-blue-500 hover:text-blue-500" },
                        { icon: Github, label: "Github", color: "hover:border-white hover:text-white" },
                        { icon: Mail, label: "Contact", color: "hover:border-neon-purple hover:text-neon-purple" },
                    ].map((btn, i) => (
                        <button
                            key={i}
                            className={clsx(
                                "p-3 rounded-xl border border-white/10 bg-white/5 text-gray-400 transition-all duration-300",
                                "hover:bg-white/10 hover:scale-110 hover:-translate-y-1",
                                btn.color
                            )}
                            title={btn.label}
                        >
                            <btn.icon size={20} />
                        </button>
                    ))}
                </div>
            </div>
        </div>
    );
}
