'use client';

import { useState, useMemo } from 'react';
import HeroSection from './components/hero/HeroSection';
import AchievementWall from './components/achievements/AchievementWall';
import CategoryFilter from './components/tools/CategoryFilter';
import ToolGrid from './components/tools/ToolGrid';
import toolsData from '@/data/tools.json';
import { Tool } from '@/types';

export default function Home() {
  const [selectedCategory, setSelectedCategory] = useState('All');

  // You could add a search state here as well, kept simple for now or integrated later

  const filteredTools = useMemo(() => {
    let tools = toolsData as Tool[];

    if (selectedCategory !== 'All') {
      tools = tools.filter(tool => tool.category === selectedCategory);
    }

    return tools;
  }, [selectedCategory]);

  return (
    <main className="min-h-screen bg-[#0a0a0a] text-[#e0e0e0] p-6 selection:bg-neon-green/30">
      <div className="max-w-6xl mx-auto flex flex-col gap-12 pb-20">

        {/* Block A: Hero Profile */}
        <HeroSection />

        {/* Block B: Achievement Wall */}
        <AchievementWall />

        {/* Block C: AI Toolbox */}
        <section>
          <div className="flex flex-col md:flex-row md:items-center justify-between gap-6 mb-8">
            <h2 className="text-neon-green font-mono text-xl flex items-center gap-2">
              <span className="opacity-50">//</span> My AI Toolbox
            </h2>
          </div>

          <CategoryFilter
            currentCategory={selectedCategory}
            onSelectCategory={setSelectedCategory}
          />

          <ToolGrid tools={filteredTools} />
        </section>

      </div>
    </main>
  );
}
